import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './5-KAIST';

export const alt = 'Yokohama, 2009';
export const distance = 3;

function Yokohama({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="right"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="center center"
        quality={70}
        src="../../../images/yokohama.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Yokohama;
