import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import { INITIAL_DISTANCE } from '../../Header/context';
import ImageSection from '../../ImageSection';

export const alt = 'Seoul, 1987';
export const distance = INITIAL_DISTANCE;

function Seoul({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt="Da-Woon's Life Path"
      distance={distance}
      prevDistance={INITIAL_DISTANCE}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="right center"
        quality={70}
        src="../../../images/seoul-left.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Seoul;
