import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './7-KAIST';

export const alt = 'Helsinki, 2010';
export const distance = 5;

function Helsinki({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="center center"
        quality={70}
        src="../../../images/helsinki.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Helsinki;
