import { StaticImage } from 'gatsby-plugin-image';
import React, {
  useCallback,
  useContext,
  useLayoutEffect,
  useState,
} from 'react';

import { HeaderContext } from '../Header/context';
import useIntersectionObserver from '../IntersectionObserver/useIntersectionObserver';
import {
  alt as prevAltD,
  distance as prevDistanceD,
} from './Da-Woon/13-Hamburg';
import { alt as prevAltM, distance as prevDistanceM } from './Minjoo/9-Hamburg';
import * as s from './style.module.scss';

export const alt = 'London';
export const distance = -1;

function London() {
  const [upRef, setUpRef] = useState<HTMLElement>();
  const [middleRef, setMiddleRef] = useState<HTMLElement>();
  const [downRef, setDownRef] = useState<HTMLElement>();

  const onUpInit = useCallback((node: HTMLElement) => {
    if (!node) return;
    setUpRef(node);
  }, []);

  const onMiddleInit = useCallback((node: HTMLElement) => {
    if (!node) return;
    setMiddleRef(node);
  }, []);

  const onDownInit = useCallback((node: HTMLElement) => {
    if (!node) return;
    setDownRef(node);
  }, []);

  const { currentEntry: entryUp } = useIntersectionObserver(upRef);
  const { currentEntry: entryMiddle } = useIntersectionObserver(middleRef);
  const { currentEntry: entryDown } = useIntersectionObserver(downRef);

  const {
    setAltLeft,
    setAltRight,
    setCaptionEntered,
    setCaptionPosition,
    setDistanceLeft,
    setDistanceRight,
    setFinalProgress,
    setFinalReveal,
  } = useContext(HeaderContext);

  useLayoutEffect(() => {
    if (!entryUp) return;

    const { isIntersecting, intersectionRatio, boundingClientRect } = entryUp;

    if (!isIntersecting) {
      if (boundingClientRect.y < 0) {
        setAltLeft('LONDON');
        setAltRight('2021');
        setCaptionPosition(1);
      } else {
        setCaptionPosition(0);
      }
      return;
    }

    if (boundingClientRect.y > 0) {
      setAltLeft(intersectionRatio > 0.5 ? 'LONDON' : prevAltD);
      setAltRight(intersectionRatio > 0.5 ? '2021' : prevAltM);
      setCaptionPosition(intersectionRatio);
      setDistanceLeft(
        (distance - prevDistanceD) * intersectionRatio + prevDistanceD,
      );
      setDistanceRight(
        (distance - prevDistanceM) * intersectionRatio + prevDistanceM,
      );
      setCaptionEntered(true);
    }
  }, [
    entryUp,
    setAltLeft,
    setAltRight,
    setCaptionEntered,
    setCaptionPosition,
    setDistanceLeft,
    setDistanceRight,
    setFinalProgress,
  ]);

  useLayoutEffect(() => {
    if (!entryMiddle) return;

    const { isIntersecting, intersectionRatio, boundingClientRect } =
      entryMiddle;

    if (!isIntersecting) return;
    setCaptionPosition(1);

    if (boundingClientRect.y > 0) {
      setFinalProgress(intersectionRatio);
    }
  }, [
    entryMiddle,
    setAltLeft,
    setAltRight,
    setCaptionPosition,
    setFinalProgress,
  ]);

  useLayoutEffect(() => {
    if (!entryDown) return;

    const { isIntersecting, intersectionRatio, boundingClientRect } = entryDown;

    if (!isIntersecting) {
      if (boundingClientRect.y < 0) {
        setFinalReveal(1);
      } else {
        setFinalReveal(0);
      }
      return;
    }

    setFinalProgress(1);

    if (boundingClientRect.y > 0) {
      setCaptionEntered(false);
      setFinalReveal(intersectionRatio);
    }
  }, [
    entryDown,
    setAltLeft,
    setAltRight,
    setCaptionEntered,
    setFinalProgress,
    setFinalReveal,
  ]);

  return (
    <section className={s.london}>
      <aside className={s.sentinel} ref={onUpInit} />
      <aside className={s.sentinel} ref={onMiddleInit} />
      <aside className={s.sentinel} ref={onDownInit} />
      <div className={s.londonIntersection}>
        <StaticImage
          alt={alt}
          objectFit="cover"
          objectPosition="center center"
          quality={70}
          src="../../images/london.jpg"
          style={{ width: '100%' }}
        />
      </div>
    </section>
  );
}

export default London;
