import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './1-Seoul';

export const alt = 'Busan, 1988';
export const distance = 3;

function Busan({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="65% center"
        quality={70}
        src="../../../images/busan.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Busan;
