import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './7-Nara';

export const alt = 'Songpa, 2017';
export const distance = 2;

function Songpa({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="right"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="44% center"
        quality={70}
        src="../../../images/songpa.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Songpa;
