import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './8-Helsinki';

export const alt = 'Muhanga, 2012';
export const distance = 8;

function Muhanga({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="75% center"
        quality={70}
        src="../../../images/muhanga.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Muhanga;
