import cx from 'classnames';
import React, {
  ReactNode,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';

import { HeaderContext } from '../Header/context';
import useIntersectionObserver from '../IntersectionObserver/useIntersectionObserver';
import * as s from './style.module.scss';

interface Props {
  className?: string;
  children: ReactNode;
  target: 'left' | 'right' | null;
  alt: string;
  prevAlt: string;
  distance: number;
  prevDistance: number;
  ratio: number;
}

function ImageSection({
  className,
  children,
  target,
  alt,
  prevAlt,
  distance,
  prevDistance,
  ratio,
}: Props) {
  const [ref, setRef] = useState<HTMLDivElement>();
  const onInit = useCallback((node: HTMLDivElement) => {
    if (!node) return;

    setRef(node);
  }, []);

  const { currentEntry } = useIntersectionObserver(ref);
  const { setDistanceLeft, setDistanceRight, setAltLeft, setAltRight } =
    useContext(HeaderContext);

  const setDistance = useMemo(
    () => (target === 'left' ? setDistanceLeft : setDistanceRight),
    [setDistanceLeft, setDistanceRight, target],
  );
  const setAlt = useMemo(
    () => (target === 'left' ? setAltLeft : setAltRight),
    [setAltLeft, setAltRight, target],
  );

  useEffect(() => {
    if (!currentEntry) return;

    const { isIntersecting, intersectionRatio, boundingClientRect } =
      currentEntry;

    if (!isIntersecting) {
      return;
    }

    if (boundingClientRect.y > 0) {
      setAlt(intersectionRatio > 0.5 ? alt : prevAlt);
      setDistance((distance - prevDistance) * intersectionRatio + prevDistance);
    }
  }, [alt, currentEntry, distance, prevAlt, prevDistance, setAlt, setDistance]);

  return (
    <section
      className={cx(className, s.section)}
      style={{ height: `${100 * ratio}vh` }}
    >
      <div className={s.intersection} ref={onInit}>
        {children}
      </div>
    </section>
  );
}

export default ImageSection;
