import React, {
  ReactNode,
  createContext,
  useEffect,
  useMemo,
  useState,
} from 'react';

interface IntersectionObserverInterface {
  observer: IntersectionObserver | null;
  currentEntries: IntersectionObserverEntry[];
}

export const IntersectionObserverContext =
  createContext<IntersectionObserverInterface>({
    observer: null,
    currentEntries: [],
  });

interface Props {
  children: ReactNode;
  options?: IntersectionObserverInit;
}

function IntersectionObserverContextProvider({ children, options }: Props) {
  const [currentEntries, setCurrentEntries] = useState<
    IntersectionObserverEntry[]
  >([]);
  const [observer, setObserver] = useState<IntersectionObserver | null>(null);

  useEffect(() => {
    setObserver(
      new IntersectionObserver((entries) => {
        setCurrentEntries(entries);
      }, options),
    );
  }, [options]);

  useEffect(
    () => () => {
      observer?.disconnect();
    },
    [observer],
  );

  const value = useMemo(
    () => ({
      observer,
      currentEntries,
    }),
    [currentEntries, observer],
  );

  return (
    <IntersectionObserverContext.Provider value={value}>
      {children}
    </IntersectionObserverContext.Provider>
  );
}

export default IntersectionObserverContextProvider;
