import React from 'react';

import Avatars from '../Avatars';
import Caption from '../Caption';
import Cities from '../Cities';
import Hero from '../Hero';
import IntersectionObserverContextProvider from '../IntersectionObserver/context';
import HeaderContextProvider from './context';
import * as s from './style.module.scss';

function Header() {
  return (
    <IntersectionObserverContextProvider
      options={{ threshold: new Array(101).fill(0).map((_, i) => i / 100) }}
    >
      <HeaderContextProvider>
        <header className={s.header}>
          <Avatars />
          <Caption />
          <Hero />
          <Cities />
        </header>
      </HeaderContextProvider>
    </IntersectionObserverContextProvider>
  );
}

export default Header;
