import React, {
  useCallback,
  useContext,
  useLayoutEffect,
  useState,
} from 'react';

import { HeaderContext } from '../Header/context';
import useIntersectionObserver from '../IntersectionObserver/useIntersectionObserver';
import DW from './Da-Woon';
import London from './London';
import MJ from './Minjoo';
import * as s from './style.module.scss';

function Cities() {
  const { setFinalProgress } = useContext(HeaderContext);
  const [ref, setRef] = useState<HTMLDivElement>();
  const onInit = useCallback((node: HTMLDivElement) => {
    if (!node) return;
    setRef(node);
  }, []);
  const { currentEntry } = useIntersectionObserver(ref);

  useLayoutEffect(() => {
    if (!currentEntry) return;

    const { isIntersecting } = currentEntry;

    if (!isIntersecting) {
      return;
    }

    setFinalProgress(0);
  }, [currentEntry, setFinalProgress]);

  return (
    <>
      <div className={s.wrap} ref={onInit}>
        <DW />
        <MJ />
      </div>

      <London />
    </>
  );
}

export default Cities;
