import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './3-Bundang';

export const alt = 'Massachusetts, 1996';
export const distance = 4;

function Massachusetts({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="right"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="60% center"
        quality={70}
        src="../../../images/massachusetts.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Massachusetts;
