import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './6-Yokohama';

export const alt = 'Nara, 2010';
export const distance = 4;

function Nara({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="right"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="44% center"
        quality={70}
        src="../../../images/nara.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Nara;
