import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './3-Saitama';

export const alt = 'Chicago, 1994';
export const distance = 5;

function Chicago({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="36% center"
        quality={70}
        src="../../../images/chicago.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Chicago;
