import { useContext, useEffect, useMemo } from 'react';

import { IntersectionObserverContext } from './context';

function useIntersectionObserver(node: Element | undefined) {
  const { observer, currentEntries } = useContext(IntersectionObserverContext);

  const currentEntry = useMemo(
    () => currentEntries.find((entry) => entry.target === node),
    [currentEntries, node],
  );

  useEffect(() => {
    if (!observer || !node) {
      return undefined;
    }

    const target = node;

    observer.observe(target as Element);

    return () => {
      observer.unobserve(target);
    };
  }, [node, observer]);

  return { observer, currentEntry };
}

export default useIntersectionObserver;
