import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';

import { ImageSectionProps } from '../../../types/ImageSection';
import ImageSection from '../../ImageSection';
import { alt as prevAlt, distance as prevDistance } from './2-Busan';

export const alt = 'Saitama, 1992';
export const distance = 2;

function Saitama({ ratio = 1 }: ImageSectionProps) {
  return (
    <ImageSection
      target="left"
      alt={alt}
      prevAlt={prevAlt}
      distance={distance}
      prevDistance={prevDistance}
      ratio={ratio}
    >
      <StaticImage
        alt={alt}
        objectFit="cover"
        objectPosition="right center"
        quality={70}
        src="../../../images/tokyo-left.jpg"
        style={{ width: '100%' }}
      />
    </ImageSection>
  );
}

export default Saitama;
