import cx from 'classnames';
import React, { useContext, useEffect, useMemo, useRef, useState } from 'react';

import { HeaderContext } from '../Header/context';
import * as s from './style.module.scss';

function Caption() {
  const [scrollbarWidth, setScrollbarWidth] = useState<number>(0);

  const ref = useRef<HTMLDivElement>(null);

  const observer = useRef<ResizeObserver | null>(null);

  useEffect(() => {
    observer.current = new ResizeObserver(() => {
      setScrollbarWidth(
        window.innerWidth - document.documentElement.clientWidth,
      );
    });

    observer.current.observe(ref.current!);

    return () => {
      observer.current?.disconnect();
    };
  }, []);

  const { altLeft, altRight, captionEntered, captionPosition } =
    useContext(HeaderContext);

  const styleLeft = useMemo(
    () => ({
      transform: `translate3d(calc(((100vw - ${scrollbarWidth}px) / 2 - 100% - 0.5ch) * ${captionPosition}), 0, 0)`,
    }),
    [captionPosition, scrollbarWidth],
  );

  const styleRight = useMemo(
    () => ({
      transform: `translate3d(calc(((100vw - ${scrollbarWidth}px) / -2 + 100% + 0.5ch) * ${captionPosition}), 0, 0)`,
    }),
    [captionPosition, scrollbarWidth],
  );

  return (
    <div className={cx(s.wrap, { [s.entered]: captionEntered })} ref={ref}>
      <div className={s.left} style={styleLeft}>
        {altLeft}
      </div>
      <div className={s.right} style={styleRight}>
        {altRight}
      </div>
    </div>
  );
}

export default Caption;
